.btn-reset {
    border: none;
    padding: 0;
    background: transparent;
    cursor: pointer;

    &:focus {
        outline: none;
    }
}

.btn {
    transition: color $transitionSpeed $transitionFunction,
        background-color $transitionSpeed $transitionFunction,
        border $transitionSpeed $transitionFunction;
}

.btn-primary {
    white-space: nowrap;
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
    @include fontManrope;

    height: 50px;
    padding: 0 31px;

    border-radius: 48px;
    border: 1px solid var(--black);
    color: var(--black);
    background-color: var(--primary);

    &:hover {
        background-color: var(--black);
        color: var(--white);
    }

    &:focus {
        background-color: var(--black);
        color: var(--white);
    }

    &.active {
        background-color: var(--black);
        color: var(--white);
        cursor: default;
        pointer-events: none;
    }

    @media (max-width:$tablet-max) {
        height: 45px;
        padding: 0 24px;
        font-size: 14px;
    }

    @media (max-width:$smallTablet-max) {
        height: 40px;
        padding: 0 18px;
        font-size: 12px;
    }
}

.btn-secondary {
    white-space: nowrap;
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;


    height: 53px;
    padding: 0 62px;

    border-radius: 19px;
    border: 1px solid var(--black);
    color: var(--black);
    background-color: var(--primary);

    &:hover {
        background-color: var(--black);
        color: var(--white);
    }

    &:focus {
        background-color: var(--black);
        color: var(--white);
    }

    @media (max-width:$tablet-max) {
        height: 47px;
        padding: 0 40px;
        font-size: 14px;
    }

    @media (max-width:$smallTablet-max) {
        height: 38px;
        padding: 0 30px;
    }
}

.btn-third {
    white-space: nowrap;

    background-color: var(--red);
    color: var(--white);
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    height: 53px;
    border-radius: 19px;
    padding: 0 55px;

    &:hover {
        background-color: var(--red-dark);
    }

    &:focus {
        background-color: var(--red-dark);
    }

    @media (max-width:$tablet-max) {
        height: 47px;
        padding: 0 40px;
        font-size: 14px;
    }

    @media (max-width:$smallTablet-max) {
        height: 38px;
        padding: 0 30px;
    }
}
